import React, { Fragment, useState, useEffect } from 'react';
import { navigate } from 'gatsby';
import menutwo from '../../img/menutwo.png';
import classes from './Filter.module.scss';
import queryString from 'query-string';

function Filter({ handleSort, location, url, srch }) {
	const parsed = queryString.parse(location.search);
	const [ clsOne, setClsOne ] = useState('null');
	const [ clsTwo, setClsTwo ] = useState('null');
	console.log(parsed);

	const handleDel = () => {
		setClsOne('null');
		setClsTwo('null');
	};

	useEffect(
		() => {
			if (parsed.sort === 'desc') {
				setClsTwo(classes.activetwo);
				setClsOne('null');
			}
			if (parsed.sort === 'asc') {
				setClsOne(classes.active);
				setClsTwo('null');
			}
		},
		[ srch ]
	);

	return (
		<Fragment>
			<div dir="rtl" className="container d-flex align-items-center mt-4">
				<img alt="filter-icon" style={{ width: '26px' }} className="img-fluid" src={menutwo} />
				<h5 className={`p-0 m-0 mr-2 ml-2 ${classes.filtertext}`}>فیلتر :</h5>
				<button className={`${classes.btns}  ${clsOne} `} onClick={() => handleSort('cheap')}>
					ارزان ترین
				</button>
				<button className={`${classes.btns} ${clsTwo} `} onClick={() => handleSort('expensive')}>
					گران ترین
				</button>
				<button
					className={classes.btns}
					onClick={() => {
						navigate(url);
						handleDel();
					}}
				>
					حذف فیلتر
				</button>
			</div>
			<div className="container">
				<hr style={{ background: '#FFFFFF', height: '1px' }} />
			</div>
		</Fragment>
	);
}

export default Filter;
